t<Modal v-if=\"modal\" size=\"large\" @close=\"closeModal\">\n\t\t\t<div class=\"modal__content\">\n\t\t\t\t<h1>{{ $t('openotp_sign', 'OpenOTP Sign') }}</h1>\n\t\t\t\t<img v-if=\"checkingSettings\" :src=\"loadingImg\">\n\t\t\t\t<p v-else-if=\"!settingsOk\"\n\t\t\t\t\tid=\"error_settings\"\n\t\t\t\t\tclass=\"alert alert-danger\"\n\t\t\t\t\tv-html=\"$t('openotp_sign', 'You have to enter the <strong>OpenOTP server URL</strong> in the <strong>OpenOTP Sign</strong> settings prior to seal any document.')\" />\n\t\t\t\t<div v-else>\n\t\t\t\t\t<img v-if=\"!success\" :src=\"mobileSigningImg\" style=\"max-width: 200px;\">\n\t\t\t\t\t<p v-else id=\"green-tick\">\n\t\t\t\t\t\t&#10003;\n\t\t\t\t\t</p>\n\t\t\t\t\t<p v-html=\"$t('openotp_sign', 'Digital seal of file <strong>{filename}</strong>', {filename: filename})\" />\n\t\t\t\t\t<p v-if=\"error\" class=\"error\">\n\t\t\t\t\t\t{{ errorMessage }}\n\t\t\t\t\t</p>\n\t\t\t\t\t<br>\n\t\t\t\t\t<div v-if=\"!requesting && !success\">\n\t\t\t\t\t\t<button type=\"button\" @click=\"seal\">\n\t\t\t\t\t\t\t{{ $t('openotp_sign', 'Seal') }}\n\t\t\t\t\t\t</button>\n\t\t\t\t\t</div>\n\t\t\t\t\t<div v-if=\"requesting\">\n\t\t\t\t\t\t<img :src=\"loadingImg\">\n\t\t\t\t\t</div>\n\t\t\t\t\t<div v-if=\"success\">\n\t\t\t\t\t\t<button type=\"button\" class=\"primary\" @click=\"closeModal\">\n\t\t\t\t\t\t\t{{ $t('openotp_sign', 'Close') }}\n\t\t\t\t\t\t</button>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t</Modal>\n\t</div>\n</template>\n<script>\nimport Modal from '@nextcloud/vue/dist/Components/Modal'\nimport EventBus from './EventBus'\nimport queryString from 'query-string'\nimport axios from '@nextcloud/axios'\nimport { generateUrl, generateFilePath } from '@nextcloud/router'\n\nexport default {\n\tname: 'OpenOTPSealModal',\n\tcomponents: {\n\t\tModal,\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tmodal: false,\n\t\t\tcheckingSettings: true,\n\t\t\trequesting: false,\n\t\t\tsuccess: false,\n\t\t\terror: false,\n\t\t\terrorMessage: '',\n\t\t\tsource: null,\n\t\t\tsettingsOk: false,\n\t\t}\n\t},\n\tmounted() {\n\t\tthis.mobileSigningImg = generateFilePath('openotp_sign', '', 'img/') + 'mobile-signing.png'\n\t\tthis.loadingImg = generateFilePath('core', '', 'img/') + 'loading.gif'\n\n\t\tEventBus.$on('ootp-seal-click', payload => {\n\t\t\tthis.showModal()\n\t\t\tthis.filename = payload.filename\n\t\t})\n\t},\n\tmethods: {\n\t\tshowModal() {\n\t\t\tthis.modal = true\n\t\t\tthis.requesting = false\n\t\t\tthis.success = false\n\t\t\tthis.error = false\n\t\t\tthis.errorMessage = ''\n\n\t\t\tconst baseUrl = generateUrl('/apps/openotp_sign')\n\n\t\t\tconst CancelToken = axios.CancelToken\n\t\t\tthis.source = CancelToken.source()\n\t\t\taxios.get(baseUrl + '/check_settings', {\n\t\t\t}, {\n\t\t\t\tcancelToken: this.source.token,\n\t\t\t})\n\t\t\t\t.then(response => {\n\t\t\t\t\tthis.checkingSettings = false\n\t\t\t\t\tthis.settingsOk = response.data\n\t\t\t\t})\n\t\t\t\t.catch(error => {\n\t\t\t\t\t// eslint-disable-next-line\n\t\t\t\t\tconsole.log(error)\n\t\t\t\t})\n\t\t},\n\t\tcloseModal() {\n\t\t\tif (this.source !== null) {\n\t\t\t\tthis.source.cancel('Operation canceled by the user.')\n\t\t\t\tthis.source = null\n\t\t\t}\n\n\t\t\tif (this.success) {\n\t\t\t\tFileList.reload()\n\t\t\t}\n\n\t\t\tthis.modal = false\n\t\t},\n\t\tseal() {\n\t\t\tthis.error = false\n\t\t\tthis.requesting = true\n\t\t\tconst baseUrl = generateUrl('/apps/openotp_sign')\n\n\t\t\tconst CancelToken = axios.CancelToken\n\t\t\tthis.source = CancelToken.source()\n\t\t\taxios.post(baseUrl + '/seal', {\n\t\t\t\tpath: this.getFilePath(),\n\t\t\t}, {\n\t\t\t\tcancelToken: this.source.token,\n\t\t\t})\n\t\t\t\t.then(response => {\n\t\t\t\t\tthis.requesting = false\n\t\t\t\t\tif (response.data.code === '1') {\n\t\t\t\t\t\tthis.success = true\n\t\t\t\t\t} else {\n\t\t\t\t\t\tthis.error = true\n\t\t\t\t\t\tthis.errorMessage = 'Error: ' + response.data.message\n\t\t\t\t\t}\n\t\t\t\t})\n\t\t\t\t.catch(error => {\n\t\t\t\t\tthis.requesting = false\n\t\t\t\t\tthis.error = true\n\t\t\t\t\tthis.errorMessage = error\n\t\t\t\t})\n\t\t},\n\t\tgetFilePath() {\n\t\t\tconst parsed = queryString.parse(window.location.search)\n\t\t\tif (parsed.dir === '/') {\n\t\t\t\treturn this.filename\n\t\t\t} else {\n\t\t\t\treturn parsed.dir + '/' + this.filename\n\t\t\t}\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\t.modal__content {\n\t\tmargin: 50px;\n\t\ttext-align: center;\n\t}\n\n\th1 {\n\t\tfont-size: 2em;\n\t\tfont-weight: bold;\n\t\tmargin-bottom: 10px;\n\t}\n\n\t#green-tick {\n\t\tfont-size: 150px;\n\t\tcolor: green;\n\t\tmargin-top: 100px;\n\t\tmargin-bottom: 100px;\n\t}\n\n\t.error {\n\t\tcolor: red;\n\t}\n\n\t.alert-danger {\n\t\tcolor: #721c24;\n\t\tbackground-color: #f8d7da;\n\t\tborder-color: #f5c6cb;\n\t}\n\n\t.alert {\n\t\tdisplay: block;\n\t\tposition: relative;\n\t\tpadding: .75rem 1.25rem;\n\t\tmargin-bottom: 1rem;\n\t\tborder: 1px solid transparent;\n\t\tborder-radius: .25rem;\n\t}\n\n\t#error_settings {\n\t\tmargin-top: 25px;\n\t}\n</style>\n"],"sourceRoot":""}]);
